import { fireDb, fireStorage, server } from '~/plugins/firebase.js'

export default {
  middleware: 'router-auth',
  async asyncData(context) {
    console.log(context)
    var docId = context.params.product_id
    var docRef = await fireDb.collection('products').doc(docId).get()
    var productDetails = {}
    if (docRef.data()) {
      productDetails = { ...docRef.data(), productId: docId }
      context.store.commit('SET_PRODUCT_DETAILS', productDetails)
    } else {
      context.router.push('/error')
      console.log('Does not exist.')
    }
    console.log(server)
    var url = server + context.route.path
    return { productDetails, url }
  },
  methods: {
    edit() {
      console.log('edit')
    },
    async getUrl() {
      await navigator.clipboard.writeText(this.url)
    },
  },
  data() {
    return {
      title: 'Home page',
    }
  },
}
