export default {
  computed: {
    imgSources: {
      get() {
        var productDetails = this.$store.state.productDetails
        return productDetails.productPhotos
      },
    },
    currentPhoto: {
      get() {
        return this.$store.state.currentPhoto
      },
      set(value) {
        this.$store.commit('SET_CURRENT_PHOTO', value)
      },
    },
  },
}
